.bottom1{
    overflow-x: hidden;
}
.btn{
    position: absolute;
    width:100%;
    height:50px;
    background-color: white;
    bottom:0;
}
.btn ul{
    margin-left: -40px;
    margin-top: 8px;
}
.btn ul li{
    display: inline-block;
    margin-left: 15px;
}
.add{
    border:1px solid blue;
    height:40px;
    width:60px;
}
.buy{
    border:1px solid blue;
    height:40px;
    width:60px;
}
.i{
    border:1px solid blue;
    width:20px;
    height:20px;

}
.back{
    position: absolute;
    top:20px;
    left:15px;
    height:25px;
    width:25px;
    background-color: #939393;
    border-radius: 50%;
    opacity: 0.6;
}

.image{
    width:100%;
    height:350px;
}
.image img{
    width:100%;
    height:350px;
}
